import React, {PureComponent} from 'react'

const box = {
    height: "100%",
};

const styles = theme => ({
    root: {
        position: "fixed",
        zIndex: 1,
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        background: "rgba(0,0,0,0.5)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
    },
    rootBox: {
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        position: "relative",
        width: 600,
        opacity: 1,
        zIndex: 2,
        backgroundColor: "#fff"
    },
    shutDown: {
        position: "absolute",
        top: 5,
        right: 5,
        cursor: "pointer"
    },
    media: {
        height: 400,
    },
    mediaBox: {
        width: "100%",
        height: "100%",
        border: "1px solid #cdcdcd"
    },
    row: {
        width: 400,
        margin: "10px 0"
    },
    buttonBox: {
        height: 50,
        lineHeight: "50px"
    },
    cancel: {
        width: 90,
        height: 30,
        border: "1px solid #cdcdcd",
        color: "#808080",
        borderRadius: 5,
        marginLeft: 30,
        cursor: "pointer"
    },
    upload: {
        width: 90,
        height: 30,
        border: 0,
        color: "#ffffff",
        borderRadius: 5,
        marginLeft: 30,
        cursor: "pointer",
        background: "rgba(44,195,253,1)",
    }
});
import {withStyles} from '@material-ui/core'

class UploadFile extends PureComponent {
    state = {
        name: '',
        path: '',
        preview: null,
        data: null,
        time: null
    };

    setBox = () => {
        this.props.change()
    };

    changeName = (e) => {
        this.setState({name: e.target.value})
    };

    //
    changePath = (e) => {
        const file = e.target.files[0];
        console.log("");
        console.log(file);
        console.log(file.lastModifiedDate);
        let date = file.lastModifiedDate.toString().split(' ');
        console.log("");
        console.log(date);
        console.log(this.state.name);
        if (!file) {
            return;
        }
        this.setState({time: date});
        let src, preview, type = file.type;
        //
        // image/
        if (/^image\/\S+$/.test(type)) {
            src = URL.createObjectURL(file);
            preview = <img style={box} src={src} alt=''/>
        }
        // video/
        else if (/^video\/\S+$/.test(type)) {
            src = URL.createObjectURL(file);
            preview = <video style={box} src={src} autoPlay loop controls/>
        }
        // text/
        else if (/^text\/\S+$/.test(type)) {
            const self = this;
            const reader = new FileReader();
            reader.readAsText(file);
            //onload
            reader.onload = function (e) {
                preview = <textarea style={box} value={this.result} readOnly></textarea>
                self.setState({path: file.name, data: file, preview: preview})
            };
            return;
        }
        this.setState({path: file.name, data: file, preview: preview})
    };

    // 
    upload = () => {
        const data = this.state.data;
        let {name, time} = this.state;
        if (!data || name === '') {
            console.log('');
            return;
        }
        console.log("");
        console.log(this.state.time);
        let month = time[1].toLocaleUpperCase();
        this.props.LocalUpload({id:111111,data:{time: `${time[2]} ${month} ${time[3]}`, name: name}}).then((data) => {
            if (data === 1) {
                alert("");
                this.props.change();
            } else {
                alert("")
            }
        });
        this.props.addData([{time: `${time[2]} ${month} ${time[3]}`, name: name}]);
        /*//urlapi
        const url = 'http://localhost:3000/api/upload';
        const form = new FormData();

        //fileapi
        form.append('file', data);
        //
        fetch(url, {
            method: 'POST',
            body: form
        }).then(res => {
            console.log(res)
        })*/
    };

    render() {
        const {classes} = this.props;
        const {name, path, preview} = this.state;
        return (
            <div className={classes.root}>
                <div className={classes.rootBox}>
                    <div className={classes.shutDown} onClick={this.setBox}></div>
                    <h4></h4>
                    <div className={classes.row}>
                        <input type='text' placeholder='' value={name} onChange={this.changeName}/>
                    </div>
                    <div className={classes.row}>
                        <div className='row-input'>
                            <input type='file' accept='video/*,image/*,text/plain' onChange={this.changePath}/>
                        </div>
                    </div>

                    <div className={classes.buttonBox}>
                        <button className={classes.upload} onClick={this.upload}></button>
                    </div>
                </div>
            </div>
        )
    }
}

export default withStyles(styles)(UploadFile)