import React, {Component} from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core'
import { Link, withRouter, Redirect} from 'react-router-dom'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import SettinsOutlined from '@material-ui/icons/SettingsOutlined'
import PermIdentity from '@material-ui/icons/PermIdentity'
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Person from '@material-ui/icons/Person';
import Lock from '@material-ui/icons/Lock'
import InputAdornment from '@material-ui/core/InputAdornment'
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { getOAuthJson, postSession} from '../../API/API'
import {styles} from './css'
import { observer, inject } from 'mobx-react'

@inject('store')
@observer
class HomePage extends Component {
    constructor(props) {
        super(props)
        this.store = props.store.authenticated//
        this.isAuthenticated = this.store.isAuthenticated//
        this.authenticated = this.store.authenticated// 
        this.sigout = this.store.sigout// 

    }
    state = {
        value: 0,//home
        username:'',//
        password: '',//
        showPassword: false,
        foucse: false,
        redirectToReferrer: false,//
    }

    formdataChange = prop => event => {
        this.setState({[prop]: event.target.value});
    };

    handleClickShowPassword = () => {
        this.setState(state => ({showPassword: !state.showPassword}));
    };

    handleChange = (event, value) => {
        this.setState({value});
    };

    handleType = () => {
        this.setState({foucse: true})
    }
    /**
     *
     *
     * @memberof HomePage
     */
    login = () => {
        const data = {username:this.state.username, password: this.state.password}
        postSession(data).then((redata) => {
            console.log(redata)
            console.log(redata.code)
            console.log(typeof redata.code)
            console.log(redata.code == 0 )
            if ( redata.code == 0) {
                this.authenticated(() =>{
                    this.setState({redirectToReferrer: true})
                })
            } else {
                this.sigout(() =>{
                    this.setState({redirectToReferrer:false})
                })
                alert("")
            }
        }).catch((err) =>{
            console.log(err)
            this.sigout(() =>{
                this.setState({redirectToReferrer:false})
            })
            alert("")
        })
    }
    /**
     *
     *github oauth
     * @memberof HomePage
     */
    oauth = () => {
        let redirect_uri = null;
        if (DEV) {
            redirect_uri = 'http://localhost:3000/user/oauth/gitcb'
        } else {
            redirect_uri = 'http://flow.dv.ailab.win/user/oauth/gitcb'
        }
        const authWin = window.open(`https://github.com/login/oauth/authorize?client_id=aeba3b94f917b3653e41&redirect_uri=${redirect_uri}&scope=user:email`, '_blank')

        const timerId = setInterval(function () {
            getOAuthJson().then((data) =>{
                if(data.code === 0 ) {
                    clearInterval(timerId)
                    authWin.close()
                    window.location.reload()
                }
            });
        })
    }
    
    render() {
        const {classes} = this.props
        let {value} = this.state
        let { from } = this.props.location.state || { from: { pathname: "/pjl" } };
        let { redirectToReferrer } = this.state;
        if (redirectToReferrer) return <Redirect to={from} />;
        return (
            <div className={classes.root}>
                <AppBar position="static" className={classes.AppRoot}>
                    <Toolbar className={classes.toolBar}>
                        <div className={classes.grow}>
                            <Typography
                                variant="h6"
                                color="inherit"
                                className={classes.growLogo}
                            >
                                QITQI
                            </Typography>
                            <Tabs value={value} onChange={this.handleChange}
                                  classes={{indicator: classes.tabsIndicator}}>
                                <Tab label=" "/>
                                <Tab label=" "/>
                                <Tab label=" "/>
                            </Tabs>
                        </div>
                        <IconButton color="inherit" aria-label="Menu" component={Link} to="/pro">
                            <SettinsOutlined/>
                        </IconButton>
                        <IconButton color="inherit" aria-label="Menu">
                            <PermIdentity/>
                        </IconButton>
                    </Toolbar>
                </AppBar>
                <div className={classes.first}>
                    <div className={classes.modle}>
                        <Typography gutterBottom variant="h5" component="h2" className={classes.textColor}>
                                   
                        </Typography>
                        <Typography component="p" className={classes.textColor}>
                            Workflow visualization platform
                        </Typography>
                        <div className={classes.loging}>
                            <div>
                                <Grid container spacing={8} alignItems="flex-end">
                                    <Grid item>
                                        <Person/>
                                    </Grid>
                                    <Grid item>
                                        <TextField id="input-with-icon-grid" label="" autoFocus={false}
                                            onChange={this.formdataChange('username')}
                                            InputLabelProps={{
                                                classes: {
                                                    root: classes.cssLabel,
                                                    focused: classes.cssFocused
                                                }
                                            }}
                                            InputProps={{
                                                classes: {
                                                    underline: classes.cssUnderline
                                                }
                                            }}
                                        />
                                    </Grid>
                                </Grid>
                            </div>
                            <input type="text" name="" type="text" style={{display: 'none'}}/>
                            <div>
                                <Grid container spacing={8} alignItems="flex-end">
                                    <Grid item>
                                        <Lock/>
                                    </Grid>
                                    <Grid item>
                                        <TextField
                                            id="filled-adornment-password"
                                            type={!this.state.foucse || this.state.showPassword ? 'text' : 'password'}
                                            label=""
                                            value={this.state.password}
                                            onChange={this.formdataChange('password')}
                                            onFocus={this.handleType}
                                            InputLabelProps={{
                                                classes: {
                                                    root: classes.cssLabel,
                                                    focused: classes.cssFocused
                                                }
                                            }}
                                            InputProps={{
                                                classes: {
                                                    underline: classes.cssUnderline
                                                },
                                                endAdornment: (
                                                    <InputAdornment position="end">
                                                        <IconButton
                                                            aria-label="Toggle password     visibility"
                                                            onClick={this.handleClickShowPassword}
                                                        >
                                                            {this.state.showPassword ?
                                                                <VisibilityOff className={classes.textColor}/> :
                                                                <Visibility className={classes.textColor}/>}
                                                        </IconButton>
                                                    </InputAdornment>
                                                ),
                                            }}
                                        />
                                    </Grid>
                                </Grid>
                            </div>
                        </div>

                        <Button  variant="contained" className={classes.loginButton} size="medium" onClick={this.login} > </Button>
                        <Button className={classes.forgetButton} onClick={this.oauth} >github</Button>
                        <Button className={classes.forgetButton} component={Link} to="/Reset">   </Button>
                        <Button className={classes.singUpButton} component={Link} to="/Register"> </Button>
                    </div>
                    <div className={classes.footer}>
                    </div>
                    <div className={classes.center}>
                    </div>
                </div>
            </div>
        )
    }
}

export default withStyles(styles)(withRouter(HomePage))